.banner-wrap {
  position: relative;
}
.banner-wrap .banner {
  width: 20rem;
  height: 8.1rem;
  position: relative;
  overflow-x: hidden;
}
.banner-wrap .container {
  position: absolute;
  width: 140rem;
  transition: left .4s ease;
}
.banner-wrap img {
  width: 20rem;
}
.banner-wrap span {
  display: inline-block;
  cursor: pointer;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
  margin-left: 0.1rem;
}
.banner-wrap .slide {
  position: absolute;
  bottom: 0.5rem;
  left: 30%;
}



