.top-header {
  position: absolute;
  top: 0;
  width: 100%;
  height: 2.4rem;
  z-index: 10010;
  background: #fff;
}
.top-header .topbox {
  width: 100%;
  height: 2.4rem;
  position: relative;
  z-index: 2;
  background: #fff;
}
.top-header .header-logo {
  width: 1.97333333rem;
  height: 2.4rem;
  margin-left: 0.42666667rem;
  position: absolute;
  background: url(~images/top-header/topheaderlogo.png) no-repeat center center / contain;
}
.top-header .header-search {
  width: 14.66666667rem;
  height: 2.34666667rem;
  margin: 0 2.66666667rem;
  padding: 0.42666667rem 0;
  position: relative;
}
.top-header .header-search .search-logo {
  display: block;
  width: 0.8rem;
  height: 0.8rem;
  background: url(~images/top-header/searchlogo.png) no-repeat center center / contain;
  position: absolute;
  top: 0rem;
  left: 0.26666667rem;
  bottom: 0rem;
  margin: auto;
}
.top-header .header-search .search-input {
  width: 100%;
  height: 1.49333333rem;
  border-radius: 0.32rem;
  font-size: 0.74666667rem;
  line-height: 0.85333333rem;
  padding-left: 1.33333333rem;
  color: #333;
  border: 0;
  outline-style: none;
  -webkit-tap-highlight-color: transparent;
  background: #eeeff3;
}
.top-header .searchopen {
  width: 16.8rem;
  margin: 0 50px 0 10px;
}
.top-header .header-navlist {
  width: 1.28rem;
  height: 1.86666667rem;
  position: absolute;
  right: 0.64rem;
  top: 0.26666667rem;
  background: url(~images/top-header/menulogo.png) no-repeat center center / contain;
}
.top-header .u-search-btn {
  position: absolute;
  top: 0rem;
  right: 0rem;
  margin: 7px 5px 4px 8px;
  height: 30px;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 2px;
  text-align: center;
  color: #188eee;
}
.top-header .search-page {
  width: 100%;
  overflow: hidden;
  background: #fff;
  position: fixed;
  left: 0rem;
  z-index: 1;
  border-top: 1px solid #eaebec;
  -webkit-transform: translate3d(0, -100%, 0);
  transform: translate3d(0, -100%, 0);
  -webkit-transition: transform .2s ease;
  transition: transform .2s ease;
}
.top-header .search-page .search-page-list {
  width: 100%;
  padding-left: 0.8rem;
}
.top-header .search-page .search-page-list .hottit {
  height: 2.13333333rem;
  line-height: 2.13333333rem;
  padding-left: 1.17333333rem;
  font-size: 0.74666667rem;
  color: #909090;
  position: relative;
}
.top-header .search-page .search-page-list .hottit > i {
  display: block;
  width: 1.17333333rem;
  height: 1.22666667rem;
  position: absolute;
  top: 0.42666667rem;
  left: 0rem;
  background: url(~images/top-header/header-list-h2logo.png) no-repeat center center / contain;
}
.top-header .search-page .search-page-list .search-page-gotlist {
  padding: 0 0 2px;
  overflow: hidden;
}
.top-header .search-page .search-page-list .search-page-gotlist li {
  float: left;
  margin-right: 10px;
  margin-bottom: 10px;
  border: 1px solid #d2deec;
  border-radius: 11px;
  padding: 0 16px;
  height: 23px;
  font-size: 14px;
  line-height: 23px;
}
.top-header .search-page .search-page-list .search-page-gotlist .hotred {
  color: red;
}
.top-header .pageopen {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.top-header .main-navlist {
  position: absolute;
  top: 2.66666667rem;
  right: 0rem;
  border-radius: 0.21333333rem;
  padding: 0.26666667rem 0.85333333rem;
  width: 9.17333333rem;
  text-align: left;
  color: #333;
  background-color: #fff;
  -webkit-transition: transform .2s ease;
  transition: transform .2s ease;
  -webkit-transform: scale(1, 0);
  transform: scale(1, 0);
}
.top-header .main-navlist li {
  padding: 0 0.21333333rem;
  height: 2.4rem;
  position: relative;
}
.top-header .main-navlist li > i {
  display: block;
  width: 1.49333333rem;
  height: 2.4rem;
  margin-right: 0.53333333rem;
  float: left;
}
.top-header .main-navlist li > span {
  font-size: 0.85333333rem;
  line-height: 2.4rem;
}
.top-header .main-navlist li:nth-of-type(1) .listicon {
  background: url(~images/top-header/iconbg1.png) no-repeat center center / contain;
}
.top-header .main-navlist li:nth-of-type(2) .listicon {
  background: url(~images/top-header/iconbg2.png) no-repeat center center / contain;
}
.top-header .main-navlist li:nth-of-type(3) .listicon {
  background: url(~images/top-header/iconbg3.png) no-repeat center center / contain;
}
.top-header .main-navlist li:nth-of-type(4) .listicon {
  background: url(~images/top-header/iconbg4.png) no-repeat center center / contain;
}
.top-header .main-navlist li::after {
  display: block;
  position: absolute;
  right: 0;
  left: 0;
  border-top: 1px solid #e1e1e1;
  content: " ";
}
.top-header .main-navlist li:last-child::after {
  border-top: 0;
  content: " ";
}
.top-header .main-navlist:before {
  display: block;
  position: absolute;
  top: -7px;
  right: 10px;
  border-right: 6px solid transparent;
  border-bottom: 8px solid #fff;
  border-left: 6px solid transparent;
  width: 0;
  height: 0;
  content: "";
}
.top-header .mainopen {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
}
.maskbg {
  position: fixed;
  top: 0rem;
  right: 0rem;
  bottom: 0rem;
  left: 0rem;
  background: rgba(0, 0, 0, 0.4);
}



